.navbar {
  padding: 1rem 0;
  
  .navbar-brand {
    font-size: 1.5rem;
    color: #2E7D32 !important;
  }
  
  .nav-link {
    font-weight: 500;
    color: #6c757d !important;
    margin: 0 0.5rem;
    transition: color 0.3s ease;
    
    &:hover {
      color: #2E7D32 !important;
    }
  }
}
